import React, { Component, FC, useEffect, useState } from 'react';
import styles from './Diary.module.scss';
import { DiaryListItem } from '@api/diary.types';
import { RouteComponentProps, withRouter } from 'react-router';
import { fetchDiaryItem } from '@src/api';

interface RouteProps {
  id: string;
}
type Props = RouteComponentProps<RouteProps>;

const Diary: FC<Props> = ({ match: { params: { id } }}) => {
  const [diary, setDiary] = useState<DiaryListItem>({
    date: '',
    title: '',
    author: '',
    mood: '',
    _id: '',
    body: '',
  });
  useEffect(() => {
    fetchDiaryItem({
      id,
    })
      .then(res => {
        setDiary(res)
      })
  }, []);
  const { date, _id, mood, author, title, body } = diary;
  return (
    <div className={styles.main}>
      <h1>{title}</h1>
      <section className={styles.content}>{body}</section>
    </div>
  );
};

export default withRouter(Diary);