import service from './service';
import { AddDiaryItemRequest, DiaryListItem, FetchDairyListResponse } from '@src/api/diary.types';

export const fetchDiaryList = (params?: object) => service.get<FetchDairyListResponse>('/essay/list', params);
export const fetchDiaryItem = (params?: object) => service.get<DiaryListItem>('/essay/item', params);
export const addDiaryItem = (params: AddDiaryItemRequest) => service.post('essay/add', params);