import React, { FC, FormEvent, Fragment } from 'react';
import styles from './DiaryWrite.module.scss';
import { Button, Form, Icon, Input, Radio } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import TextArea from 'antd/lib/input/TextArea';
import { AddDiaryItemRequest } from '@api/diary.types';
import { addDiaryItem } from '@src/api';

const { Item } = Form;

const DiaryWrite: FC<FormComponentProps<AddDiaryItemRequest>> = ({ form: { validateFields, getFieldDecorator } }) => {
  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    validateFields((err, params) => {
      if (!err) {
        addDiaryItem({
          ...params,
          author: '逗豆',
        })
          .then(() => {
            // todo change
            window.location.href = '/diary/list'
          })
      }
    });
  };

  return (
    <Fragment>
      <Form className={styles.form} onSubmit={handleSubmit}>
        <Item label="Title">
          {getFieldDecorator('title', {
            rules: [{ required: true, max: 20 }],
          })(
            <Input type="text" placeholder="title" />
          )}
        </Item>
        <Item label="Content">
          {getFieldDecorator('content', {
            rules: [{ required: true, max: 1000 }],
          })(
            <TextArea rows={5} placeholder="content"/>
          )}
        </Item>
        <Item
          className={styles.item}
          label="Radio.Group"
        >
          {getFieldDecorator('mood', {
            rules: [{ required: true, message: 'Please choose your mood !' }],
          })(
            <Radio.Group>
              <Radio value="smile" className={styles.radio}>
                <Icon type='smile'/> 开心
              </Radio>
              <Radio value="meh" className={styles.radio}>
                <Icon type='meh'/> 一般
              </Radio>
              <Radio value="frown" className={styles.radio}>
                <Icon type='frown'/> 伤心
              </Radio>
            </Radio.Group>
          )}
        </Item>
        <Item>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Item>
      </Form>
    </Fragment>
  );

};

export default Form.create()(DiaryWrite);